.mus-header-parent {
  position: relative;
}

@media only screen and (max-width: 767px) {
  .mus-header-parent {
    height: calc(100% - 80px);
    height: calc(100vh  - 80px);
  }
}

.mus-header-parent .mus-image-area {
  background: #ce4283;
  /* Old browsers */
  /* IE9 SVG, needs conditional override of 'filter' to 'none' */
  background: -moz-linear-gradient(top, #ce4283 0%, #b3465d 100%);
  /* FF3.6+ */
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #ce4283), color-stop(100%, #b3465d));
  /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(top, #ce4283 0%, #b3465d 100%);
  /* Chrome10+,Safari5.1+ */
  background: -o-linear-gradient(top, #ce4283 0%, #b3465d 100%);
  /* Opera 11.10+ */
  background: -ms-linear-gradient(top, #ce4283 0%, #b3465d 100%);
  /* IE10+ */
  background: linear-gradient(to bottom, #ce4283 0%, #b3465d 100%);
  /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='$begin_color', endColorstr='$end_color', GradientType=0);
  /* IE6-8 */
  position: relative;
  min-height: 640px;
}

@media only screen and (max-width: 767px) and (orientation: landscape) {
  .mus-header-parent .mus-image-area {
    min-height: calc(100vh - 160px -  80px );
  }
}

@media only screen and (max-width: 767px) and (orientation: portrait) {
  .mus-header-parent .mus-image-area {
    min-height: calc(100vh - 160px -  80px );
  }
}

.mus-header-parent .mus-image-area .count {
  z-index: 999999;
  position: absolute;
  left: 50%;
  top: 98%;
  transform: translate(-48%, -40%);
}

.mus-header-parent .mus-heder-center-image {
  /*
        // desktop first approach;
        min-width: 330px;
        max-width: 641px;
        background: url(../images/hero-img@2x.png);
        background-repeat: no-repeat;
        height: 100%;
        background-size: contain;
        background-position: 50% 50%;
        position: relative;
        transform: translate(-50%, -50%);
        top: 50%;
        left: 50%;
        @include mq('(max-width:767)') {
            background-position: 50% 70%;
            min-width: 277px;
            background: url(../images/hero-img@2x.png);
        }*/
  background-position: 50% 70%;
  min-width: 277px;
  max-width: 641px;
  background: url(../images/hero-img@2x.png);
  background-repeat: no-repeat;
  height: 100%;
  background-size: contain;
  background-position: 50% 50%;
  position: relative;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
}

@media only screen and (min-width: 768) {
  .mus-header-parent .mus-heder-center-image {
    max-width: 641px;
    background-position: 50% 50%;
    background: url(../images/hero-img@2x.png);
  }
}

.mus-header-parent .mus-search-area {
  min-height: 80px;
  display: flex;
  background-color: #333441;
  align-items: center;
  justify-content: center;
  background-color: #333441;
  position: relative;
  background-color: #333441;
}

.mus-header-parent .mus-search-area .mus-search-bar {
  width: 60%;
  max-width: 641px;
  height: 56px;
  background-color: #fefefe;
  border: 0.5px solid #494b5e;
  border-radius: 28px;
  position: relative;
  display: flex;
  align-items: center;
  font-family: "Raleway";
}

.mus-header-parent .mus-search-area .mus-search-bar .loader-ring {
  position: absolute;
  height: 40px;
  width: 40px !important;
  width: 40 px;
  transform: translateX(-50%);
  left: 46.5%;
  border: 4px solid rgba(56, 58, 73, 0.2);
  border-top: 4px solid #b3465d;
}

@media only screen and (max-width: 767px) {
  .mus-header-parent .mus-search-area .mus-search-bar {
    width: 90%;
    position: absolute;
    transform: translateY(-110%);
    top: 50%;
  }
  .mus-header-parent .mus-search-area .mus-search-bar .loader-ring {
    position: absolute;
    height: 30px;
    width: 30px !important;
    width: 40 px;
    transform: translateX(-50%);
    left: 46%;
    border: 4px solid rgba(56, 58, 73, 0.2);
    border-top: 4px solid #b3465d;
  }
  .mus-header-parent .mus-search-area .mus-search-bar input[type="text"] {
    width: 100%;
  }
